.homepage {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0;
}

h1 {
  font-size: 50px;
  color: rgb(209, 207, 207);
  font-weight: 400;
  text-align: center;
  -webkit-text-fill-color: white; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: rgb(59, 59, 59);
  line-height: 4rem;
}

.topPart {
  background-image: url("../../images/sushi_logo_white_transparent.png");
  background-repeat: no-repeat;
  background-size: 350px 120px;
  /* background-size: cover; */
  background-position: center;
  display: flex;
  flex: 1;
  justify-content: center;
  width: 100%;
}

.middlePart {
  display: flex;
  width: 100%;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.bottomPart {
  display: flex;
  width: 100%;
  flex: 1;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.trainRunning {
  max-width: 350px;
  width: auto;
  padding: 0;
  color: rgb(241, 241, 241);
  font-family: "Yesteryear", cursive;
  font-weight: 500;
  text-align: center;
  align-items: center;
  display: flex;
  flex: 1;
  font-size: 25px;
  background-color: rgba(119, 153, 106, 0.5);
  padding: 10px;
  margin-bottom: 40%;
  box-shadow: 0px 0px 20px rgba(119, 153, 106, 0.7);
}

@media screen and (min-width: 500px) {
  h1 {
    font-size: 70px;
  }
  .header_container {
    max-height: 50%;
  }
  .topPart {
    background-size: 600px 200px;
  }
  .bottomPart {
    flex: 1;
  }
  .bottomPart {
    flex: 1;
  }
  .trainRunning {
    padding: 0 35px 0 35px;
    margin: 30px;
    font-size: 30px;
  }
}
